.loading {
  position: fixed;
  top: 90px;
  left: 0;
  width: 100%;
  height: calc(100% - 90px);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-flow: column;
      flex-flow: column;
}

.loading__icon img {
  height: 100px;
  width: auto;
}

.loading__text {
  font-size: 17pt;
  font-style: italic;
  text-align: center;
  margin-top: 20px;
  color: rgba(0, 0, 0, 0.4);
}


